:root{
    --scale-factor: 1.75;
    /* for some reason, the calc is smaller than the orignal version */
}

#calculator-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    user-select: none;
}

#calculator{
    /* transform: 1.75; */
    /* transform: var(--scale-factor); */
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 25px;
}

#calcBody{
    /* width: calc(55vh / 1.75); */
    
    /* width: calc(55vh / var(--scale-factor)); */
    margin: auto auto;
}

#panel, #numbers, #operators{
    display: grid;
}

#numbers, #operators{
    gap: 2px;
    /* justify-items: center;
    align-items: center; */

    /* grid-auto-columns: minmax(200px, auto);
    grid-auto-rows: minmax(200px, auto); */
    /* i'm trying to make every character box of uniform size, i.e. i want the size to be that of the biggest cell
    it's not possible to find out the biggest cell with css alone so, let's divide the grid into areas of uniform content
    we have
    a) numbers
    b) row below numbers
    c) operators

    or we can try to rely on stretch. biggest components are supposed to influence their rows & columns
     */

     /* why is it that 0 is a bit shifted to the left in its column? 
     cause column-gap is applied for every row, so there is no 'global alignment'*/
}

#numbers{
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(3, 1fr)
}

#operators{
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(1, 1fr)
}

#panel{
    column-gap: 1em;
    text-align: center;
}

#panel{
    grid-template-columns: 70% 1fr;
    position: relative;
}

.gridCell{
    padding: 10px;
    /* font-size: 1.2rem; */
}

.gridCell:hover, #deleteButton:hover{
    cursor: pointer;
    opacity: 0.6;
}

#equalsSign:hover{
    color: rgba(0, 255, 255, 0.678);
}
#screen{
    border-bottom: 1px solid lightgray;
    padding-left: 5px;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#screen input {
    border-bottom: none;
    margin-bottom: 0;
}

#deleteButton{
    font-size: 0.6em;
    margin-right: 1em;
}

#notification{
    position: absolute;
    top: 5px;
    left: 1em;
    font-size: 0.7rem;
}