@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;1,900&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0/css/materialize.min.css);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons|Material+Icons+Outlined);
body {
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
:root{
    --application-side-size: 70vh;
    --main-red: #ee6e73;
    --main-blue: #2196F3;
}

.brand-logo .badge{
    margin-top: 10px;
}

.App, main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.App{
    height: 100vh;
}



nav .active{
    transition: all 0.5s;
    background-color: rgba(0,0,0,0.1)
}

main{
    flex-grow: 1;
    width: 100%;
}

nav{
    flex-shrink: 0;
}

main > *{
    max-width: 70vh;
    max-width: var(--application-side-size);
    max-height: 70vh;
    max-height: var(--application-side-size);
}

/* i have set the height of .App element to 100vh
and now i'm trying to make it so that the second child of the element takes up all available space
and that's possible with a little bit of flexbox (flex-grow: 1)
 */
#home{
    padding: 30px;
}
.ghost-task{
    opacity: 0.5;
}

.todos{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.todos .todo{
    cursor: pointer;
}
:root{
    --scale-factor: 1.75;
    /* for some reason, the calc is smaller than the orignal version */
}

#calculator-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

#calculator{
    /* transform: 1.75; */
    /* transform: var(--scale-factor); */
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 25px;
}

#calcBody{
    /* width: calc(55vh / 1.75); */
    
    /* width: calc(55vh / var(--scale-factor)); */
    margin: auto auto;
}

#panel, #numbers, #operators{
    display: grid;
}

#numbers, #operators{
    grid-gap: 2px;
    gap: 2px;
    /* justify-items: center;
    align-items: center; */

    /* grid-auto-columns: minmax(200px, auto);
    grid-auto-rows: minmax(200px, auto); */
    /* i'm trying to make every character box of uniform size, i.e. i want the size to be that of the biggest cell
    it's not possible to find out the biggest cell with css alone so, let's divide the grid into areas of uniform content
    we have
    a) numbers
    b) row below numbers
    c) operators

    or we can try to rely on stretch. biggest components are supposed to influence their rows & columns
     */

     /* why is it that 0 is a bit shifted to the left in its column? 
     cause column-gap is applied for every row, so there is no 'global alignment'*/
}

#numbers{
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(3, 1fr)
}

#operators{
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(1, 1fr)
}

#panel{
    grid-column-gap: 1em;
    -webkit-column-gap: 1em;
            column-gap: 1em;
    text-align: center;
}

#panel{
    grid-template-columns: 70% 1fr;
    position: relative;
}

.gridCell{
    padding: 10px;
    /* font-size: 1.2rem; */
}

.gridCell:hover, #deleteButton:hover{
    cursor: pointer;
    opacity: 0.6;
}

#equalsSign:hover{
    color: rgba(0, 255, 255, 0.678);
}
#screen{
    border-bottom: 1px solid lightgray;
    padding-left: 5px;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#screen input {
    border-bottom: none;
    margin-bottom: 0;
}

#deleteButton{
    font-size: 0.6em;
    margin-right: 1em;
}

#notification{
    position: absolute;
    top: 5px;
    left: 1em;
    font-size: 0.7rem;
}
#field{
    margin: 0;
}

.disabled{
    opacity: 0.5
}

#game{
    color: inherit;
}

.grid{
    min-height: 40vh;
    min-width: 40vh;
    display:grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(3, 1fr);
}

#game .gridCell{
    border-right: 1px solid rgba(0,0,0,0.12);
    border-bottom: 1px solid rgba(0,0,0,0.12);
    display: flex;
    align-items: center;
    justify-content: center;
}

#game .gridCell:hover{
    background-color: rgba(0,0,0,0.12);
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.circle, .cross{
    height: 65%;
    width: 65%;
}

.grid .gridRow .gridCell:last-child{
    border-right: 0;
}

#game .gridRow{
    display: grid;
    border-bottom: 1px solid rgba(0,0,0,0.12);
    grid-template-columns: repeat(3, 1fr);
}

#game .grid .gridRow:last-child{
    border-bottom: 0;
}

.game-controls li a i {
    line-height: unset;
    color: black;
}

.gameHeader{
    margin-bottom: 30px;
}

#game .winner{
    background-color: var(--main-blue)
}
